.Container {
    position: relative;
    min-height: 300px;
    max-height: 800px;
    overflow: hidden;
    margin-bottom: 50px;
}

/* Style the video: 100% width and height to cover the entire window */
.Video {
    width: 50%;
    height: 50%;
    display: block;
    margin: 0 auto;
  }
  
  /* Add some content at the bottom of the video/page */
  .Content {
    position: absolute;
    top: 0;
    color:#f1f1f1;
    background-color: rgba(0, 0, 0, 0.3);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
  }
  .SubContent {
      text-align: center;
      padding: 10px;
    }
  .SubContent p {
      color: #eeeeeead;
      font-size: 1.3rem;
  }
  .SubContent img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    display: block;
    margin: 15px auto;
    border: #f1f1f1 1px solid;
  }